import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Grid from "@material-ui/core/Grid";
import Meal from "@cvccorp-components/icons/dist/Meal";
import Typography from "../../Typography";
import Button from "../../Button";
import Tag from "../../Tag";
import Box from "../../Box";
import Price from "../Price";
import Info from "../Info";
import Amenities from "../Amenities";
import { styles } from "./styles";

const uuidv4 = require("uuid/v4");

const List = ({
    classes,
    onClick,
    thumb,
    title,
    onDetail,
    about,
    amenities,
    options,
    product,
    pax,
    days,
}) => {
    return (
        <Box className={classes.root} onClick={onClick}>
            <div className={classes.content}>
                <Grid container spacing={2} alignItems="center">
                    <Grid item xs={2}>
                        <div className={classes.imgContainer}>
                            <img className={classes.thumb} src={thumb} alt="" />
                        </div>
                    </Grid>
                    <Grid item xs={5}>
                        <h1 className={classes.title}>
                            <Typography type="body2semibold">{title}</Typography>
                        </h1>
                        {onDetail && (
                            <Button
                                className={classes.detailBtn}
                                color="primary"
                                outline
                                label="Ver detalhes"
                                variant="text"
                                onClick={onDetail}
                            />
                        )}
                    </Grid>
                </Grid>
                {about && (
                    <div className={classes.about}>
                        <Typography type="body2semibold">Sobre o quarto</Typography>
                        <Typography type="body2regular" className={classes.aboutText}>
                            {about}
                        </Typography>
                    </div>
                )}
                {amenities && <Amenities amenities={amenities} />}
            </div>
            <ul className={classes.footer}>
                {options &&
          options.map(item => (
              <li key={uuidv4()} className={classes.footerITem}>
                  <div className={classes.info}>
                      <div className={classes.infoHeader}>
                          <Typography type="headline6" className={classes.infoTitle}>
                              {item.title}
                          </Typography>
                          {item.meal && <Tag label={item.meal} icon={<Meal />} />}
                      </div>
                      <Info
                          className={classes.infoContainer}
                          bedInfo={item.bedInfo}
                          noRefundable={item.noRefundable}
                      />
                  </div>
                  <div className={classes.price}>
                      <Price
                          onClick={item.onClick}
                          product={product}
                          totalPrice={item.totalPrice}
                          partialPrice={item.partialPrice}
                          totalPeople={pax}
                          days={days}
                          taxesIncluded={item.taxesIncluded}
                          parcel={item.parcel}
                      />
                  </div>
              </li>
          ))}
            </ul>
        </Box>
    );
};

List.propTypes = {
    classes: PropTypes.object.isRequired,
    onClick: PropTypes.func,
    thumb: PropTypes.string,
    title: PropTypes.string.isRequired,
    product: PropTypes.string.isRequired,
    pax: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    days: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired,
    onDetail: PropTypes.func.isRequired,
    about: PropTypes.string,
    amenities: PropTypes.array,
    options: PropTypes.array.isRequired,
};

export default withStyles(styles, { withTheme: true })(List);
